// lib/services/orderService.ts

import { authHeader } from "./userService";

export interface PurchasePayload {
  id_user: number;
  status: "pending" | "paid" | "cancelled";
  total: number;
}
export interface DetailPayload {
  id_product: number;
  quantity: number;
  unit_price: number;
}

/** El payload que enviamos al backend */
export interface OrderPayload {
  purchase: PurchasePayload;
  details: DetailPayload[];
}

const API_BASE_URL = "/api/8082/delivery-service";

/**
 * POST /purchase
 */
export async function createOrder(
  payload: OrderPayload
): Promise<{ id: number }> {
  const res = await fetch(`${API_BASE_URL}/purchase`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      ...authHeader(),
    },
    body: JSON.stringify({
      purchase: payload.purchase,
      details: payload.details,
    }),
  });

  if (res.status === 401) {
    throw new Error("No autorizado. Por favor, inicia sesión de nuevo.");
  }
  if (!res.ok) {
    const err = await res.json().catch(() => null);
    throw new Error(err?.error || "Error al crear el pedido");
  }

  // El handler devuelve { message, id }
  return res.json();
}
