"use client";

import { useState, type ReactElement, useMemo } from "react";
import Image from "next/image";
import { useForm, FormProvider, SubmitHandler } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";

import AddressForm from "./components/AddressForm";
import OrderItems from "./components/OrderItems";
import SummaryBox, { calcFeesUSD } from "./components/SummaryBox";
import { useCart } from "@/lib/cart-context";
import { createOrder, type OrderPayload } from "@/lib/services/orderService";
import { getTotalPromo } from "@/lib/precio-producto";

// --- Esquema y Tipos para la validación del formulario ---
const checkoutSchema = z.object({
    address: z.string().min(10, {
        message: "La dirección es requerida (mín. 10 caracteres).",
    }),
    instructions: z.string().optional(),
});
type CheckoutFormData = z.infer<typeof checkoutSchema>;

export default function PagoPage(): ReactElement {
    const [orderId, setOrderId] = useState<string | null>(null);
    const { items, clear: clearCart } = useCart();

    const methods = useForm<CheckoutFormData>({
        resolver: zodResolver(checkoutSchema),
    });
    const {
        handleSubmit,
        formState: { isSubmitting },
    } = methods;

    // --- Lógica de cálculo de costos para el payload ---
    const { total } = useMemo(() => {
        const sub = items.reduce(
            (sum, it) =>
                sum + getTotalPromo(it.promo, it.price, it.quantity).total,
            0
        );
        // Asumimos que la propina no se envía al backend, solo se usa para el pago.
        // Si necesitas enviarla, deberías manejar su estado aquí.
        const { envio, servicio } = calcFeesUSD(sub);
        const finalTotal = sub + envio + servicio; // Total sin propina para el backend
        return { subtotal: sub, total: finalTotal };
    }, [items]);

    // --- Función que se ejecuta al enviar el formulario válido ---
    const processOrder: SubmitHandler<CheckoutFormData> = async () => {
        //  OBTENER ID DE USUARIO (contexto de usuario o autenticación)
        const id_user = 2; // TODO: Reemplazar con el ID del usuario logueado

        // TRANSFORMAR ITEMS DEL CARRITO AL FORMATO "details"
        const details = items.map((item) => ({
            id_product: Number(item.id),
            quantity: item.quantity,
            unit_price: getTotalPromo(item.promo, item.price, 1).total,
        }));

        // CONSTRUIR EL OBJETO "purchase"
        const purchase = {
            id_user,
            status: "pending" as const,
            total: total,
        };

        //  CONSTRUIR EL PAYLOAD FINAL
        const orderPayload: OrderPayload = {
            purchase,
            details,
        };

        // LLAMAR AL SERVICIO
        try {
            const response = await createOrder(orderPayload);
            console.log("Respuesta del backend:", response);
            clearCart();

            // TODO: Manejar la respuesta del backend según sea necesario
            setOrderId(response.id || "REVISAR-CONFIRMACION");
        } catch (error) {
            console.error("Error al crear el pedido:", error);
            alert(
                "Hubo un problema al procesar tu pedido. Por favor, intenta de nuevo."
            );
        }
    };

    /* ----------------------------- Render UI ----------------------------- */
    return (
        <div className="relative isolate flex-1 flex items-center justify-center px-4 sm:px-8 py-8">
            <Image
                src="/images/superr.jpg"
                alt="Background supermercado Kwik-E-Mart"
                fill
                className="-z-20 object-cover"
                priority
            />
            <div className="absolute inset-0 -z-10 bg-gray-900/40 backdrop-blur-sm" />

            <div className="bg-[#1e1e1e]/90 backdrop-blur-sm rounded-2xl p-8 shadow-2xl w-full max-w-6xl text-white">
                {orderId ? (
                    <div className="flex flex-col items-center text-center gap-6 py-16">
                        <h2 className="text-3xl font-bold text-green-400">
                            ¡Gracias por tu compra!
                        </h2>
                        <p className="text-lg">
                            Tu número de pedido es:
                            <br />
                            <span className="font-mono text-yellow-300 text-2xl">
                                {orderId}
                            </span>
                        </p>
                        <p className="text-sm text-gray-300 max-w-md">
                            Recibirás un correo con los detalles y
                            actualizaciones del envío.
                        </p>
                    </div>
                ) : (
                    <FormProvider {...methods}>
                        <form onSubmit={handleSubmit(processOrder)}>
                            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                                <div className="space-y-6">
                                    <AddressForm />
                                    <OrderItems />
                                </div>
                                <div className="flex flex-col gap-4">
                                    <p className="font-simpson text-outline-blue text-yellow-300 text-center text-lg">
                                        ¡Gracias, vuelva pronto… y recuerde: por
                                        cada donut la quinta fuga de Squishee es
                                        cortesía de la casa!
                                    </p>
                                    <SummaryBox isProcessing={isSubmitting} />
                                </div>
                            </div>
                        </form>
                    </FormProvider>
                )}
            </div>
        </div>
    );
}
